<template>
  <div class="app-content">
    <div class="btns">
      <div>
        <el-button type="primary" @click="onTriggerByLine(`blueArrow`)">+</el-button>
        <el-button type="primary" @click="onTriggerByLine(`grayLarge`)">-</el-button>
        <el-button type="primary" @click="onTriggerByLine(`graySmall`)">-</el-button>
      </div>
      <div>
        
        <el-switch v-model="isEditMode" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
      </div>
      <div>
        <el-button type="primary" @click="onTriggerIsEdgeWithArrow"></el-button>
        <el-button type="primary" @click="onTriggerIsEdgeWithArrow"></el-button>
      </div>
      <div>
        <el-button type="primary" @click="onSave"></el-button>
        <el-button type="primary" @click="onLoadBylocalStorage"></el-button>
      </div>
    </div>

    <el-drawer title="" :visible.sync="visible" append-to-body>
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="">
          <el-input v-model="form.label"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-color-picker v-model="form.color"></el-color-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onConfirm"></el-button>
          <el-button @click="onCancel"></el-button>
        </el-form-item>
      </el-form>
    </el-drawer>

    <el-dialog
      ref="dialogRef"
      :title="currentTitle"
      :visible.sync="dialogVisible"
      width="660px"
      :modal="false"
      append-to-body
      v-draggable-dialog
      :style="dialogStyle"
      @opened="onOpened"
    >
      <div slot="title" class="modify-dialog-title">{{ currentTitle }}</div>
      <div>
        <el-form ref="formByDetalRef" :model="formByProcess" label-width="140px">
          <el-row>
            <el-col :span="12">
              <el-form-item label="xx">
                <span class="value">{{ formByProcess.prop01 }}</span>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="xx">
                <span class="value">{{ formByProcess.prop02 }}</span>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="x">
                <span class="value">{{ formByProcess.prop03 }}</span>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="xx">
                <span class="value">{{ formByProcess.prop04 }}</span>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="xxx">
                <span class="value">{{ formByProcess.prop05 }}</span>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="xx">
                <span class="value">{{ formByProcess.prop06 }}</span>
              </el-form-item>
            </el-col>
            <el-col :span="24">
              <el-form-item label="xx">
                <span class="value">{{ formByProcess.prop07 }}</span>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="">
                <span class="value">{{ formByProcess.prop08 }}</span>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="">
                <span class="value">{{ formByProcess.prop09 }}</span>
              </el-form-item>
            </el-col>
            <el-col :span="24">
              <el-form-item label="">
                <span class="value">{{ formByProcess.prop10 }}</span>
              </el-form-item>
            </el-col>
            <el-col :span="24">
              <el-form-item label="">
                <el-button type="text" @click="goToPath(formByProcess.prop12)">{{ formByProcess.prop11 }}</el-button>
                <!-- <span class="value">{{ formByProcess.prop11 }}</span> -->
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <!-- <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
      </span> -->
    </el-dialog>

    <div class="side-part" id="stencil">side</div>
    <div id="container" ref="containerRef"></div>
  </div>
</template>

<script>
import { Clipboard, Graph, History, Keyboard, Selection, Shape, Snapline, Stencil, Transform, Scroller } from '@antv/x6'

import nodeData from './nodeData.js'

export default {
  name: 'AntvPage',
  data() {
    return {
      graphObj: null,
      visible: false,
      form: {
        label: '',
        color: '',
      },
      currentNode: null,
      isEdgeWithArrow: true, // 
      isEditMode: true, // 
      dialogVisible: false,
      formByProcess: {
        prop01: `xx`,
        prop02: `xx`,
        prop03: ``,
        prop04: ``,
        prop05: ``,
        prop06: ``,
        prop07: `2027421-2027912`,
        prop08: ``,
        prop09: `2026.06.15 `,
        prop10: ``,
        prop11: ``,
        prop12: ``,
      },
      dialogStyle: {
        position: 'fixed',
        top: '0px',
        left: '0px',
        margin: '0', //  Element  margin: auto
      },
      currentTitle: ``,
      lineType: ``,
    }
  },
  mounted() {
    let graphContainer = this.$refs.containerRef

    console.log(`graph width => ${graphContainer.clientWidth}`)
    console.log(`graph height => ${graphContainer.clientHeight}`)

    this.initGraph(graphContainer.clientWidth, graphContainer.clientHeight)

    setTimeout(() => {
      if (localStorage.getItem(`x6JSON`)) {
        console.log(``)
        this.onLoadBylocalStorage()
      } else {
        console.log(``)
        this.onLoadByDefaultData()
      }
    }, 800)
  },
  methods: {
    // 
    initGraph(containerWidth = 800, containerHeight = 800) {
      const graph = new Graph({
        container: document.getElementById('container'),
        width: containerWidth,
        height: containerHeight,
        background: {
          color: '#F2F7FA', // 
        },
        grid: {
          size: 10, //  10px
          visible: true, //  dot 
        },
        interacting: true, // NOTE: 
        panning: {
          enable: true, // 
          modifiers: ['alt', 'ctrl'],
        },
        mousewheel: {
          enabled: true, // 
          minScale: 0.2,
          maxScale: 1.5,
        },
        // selecting: true, //  Shift/Ctrl 
        magnetThreshold: 0, //  0
        connecting: {
          //  5. 
          allowBlank: false, // 
          allowNode: true, // 
          allowEdge: true, // 
          allowLoop: true, // 
          validateMagnet() {
            return true
          }, // 
          router: {
            name: 'manhattan', // 
            // args: { padding: 20 },
          },
          connector: {
            name: 'rounded', //
            args: { radius: 8 },
          },
          anchor: 'center',
          connectionPoint: 'anchor',
          snap: {
            radius: 20,
          },
          // NOTE: isEdgeWithArrow
          createEdge: () => {
            let lineConfig = null
            switch (this.lineType) {
              case `blueArrow`:
                lineConfig = {
                  stroke: '#2590FF',
                  strokeWidth: 4,
                  targetMarker: {
                    name: 'block', // 
                    width: 6,
                    height: 12,
                  },
                }
                break
              case `grayLarge`:
                lineConfig = {
                  stroke: '#6584A4',
                  strokeWidth: 2,
                  targetMarker: null,
                }
                break
              case `graySmall`:
                lineConfig = {
                  stroke: '#6584A4',
                  strokeWidth: 1,
                  targetMarker: null,
                }
                break
              default:
                lineConfig = {
                  stroke: '#2590FF',
                  strokeWidth: 4,
                  targetMarker: {
                    name: 'block', // 
                    width: 6,
                    height: 12,
                  },
                }
                break
            }
            return new Shape.Edge({
              attrs: {
                line: lineConfig,
              },
              zIndex: 0,
            })
          },
        },
      })

      this.initPlugin(graph)
      this.initKeyBoardAndEvent(graph)
      this.initNodePorts(graph)
      this.initNodeEvent(graph)
      this.initNode()
      this.initStencil(graph)

      // // NOTE:  start
      // // this.initPlugin(graph)
      // this.initKeyBoardAndEvent(graph)
      // // this.initNodePorts(graph)
      // this.initNodeEvent(graph)
      // this.initNode()
      // // this.initStencil(graph)
      // // NOTE:  end

      this.graphObj = graph
    },
    // 
    initPlugin(graph) {
      graph
        .use(
          new Transform({
            resizing: true, // 
            rotating: true, // 
          }),
        )
        .use(
          new Selection({
            multiple: true, //  ctrl  command 
            rubberband: true, // 
            showNodeSelectionBox: true, // 
          }),
        )
        .use(new Snapline())
        .use(new Keyboard())
        .use(new Clipboard())
        .use(new History())
      // .use(
      //   new Scroller({
      //     pannable: false, // 
      //     pageVisible: false, // 
      //     pageBreak: false, // 
      //   }),
      // )
    },
    // 
    initKeyBoardAndEvent(graph) {
      graph.bindKey(['meta+c', 'ctrl+c'], () => {
        const cells = graph.getSelectedCells()
        if (cells.length) {
          graph.copy(cells)
        }
        return false
      })
      graph.bindKey(['meta+x', 'ctrl+x'], () => {
        const cells = graph.getSelectedCells()
        if (cells.length) {
          graph.cut(cells)
        }
        return false
      })
      graph.bindKey(['meta+v', 'ctrl+v'], () => {
        if (!graph.isClipboardEmpty()) {
          const cells = graph.paste({ offset: 32 })
          graph.cleanSelection()
          graph.select(cells)
        }
        return false
      })

      // undo redo
      graph.bindKey(['meta+z', 'ctrl+z'], () => {
        if (graph.canUndo()) {
          graph.undo()
        }
        return false
      })
      graph.bindKey(['meta+shift+z', 'ctrl+shift+z'], () => {
        if (graph.canRedo()) {
          graph.redo()
        }
        return false
      })

      // select all
      graph.bindKey(['meta+a', 'ctrl+a'], () => {
        const nodes = graph.getNodes()
        if (nodes) {
          graph.select(nodes)
        }
      })

      // delete
      graph.bindKey('backspace', () => {
        const cells = graph.getSelectedCells()
        if (cells.length) {
          graph.removeCells(cells)
        }
      })

      // zoom
      graph.bindKey(['ctrl+1', 'meta+1'], () => {
        const zoom = graph.zoom()
        if (zoom < 1.5) {
          graph.zoom(0.1)
        }
      })
      graph.bindKey(['ctrl+2', 'meta+2'], () => {
        const zoom = graph.zoom()
        if (zoom > 0.5) {
          graph.zoom(-0.1)
        }
      })
    },
    // /
    initNodePorts(graph) {
      const showPorts = (ports, show) => {
        for (let i = 0, len = ports.length; i < len; i += 1) {
          ports[i].style.visibility = show ? 'visible' : 'hidden'
        }
      }
      graph.on('node:mouseenter', () => {
        const container = document.getElementById('container')
        const ports = container.querySelectorAll('.x6-port-body')
        showPorts(ports, true)
      })
      graph.on('node:mouseleave', () => {
        const container = document.getElementById('container')
        const ports = container.querySelectorAll('.x6-port-body')
        showPorts(ports, false)
      })
    },
    // 
    initNodeEvent(graph) {
      // graph.on('node:contextmenu', ({ e, x, y, node, view }) => {
      //   console.log('node:contextmenu', e, x, y, node, view)
      //   this.visible = true
      //   this.currentNode = node
      //   this.form.label = node.label
      //   // node.setAttrs({
      //   //   label: {
      //   //     text: '',
      //   //   },
      //   // })
      // })
      graph.on('node:click', ({ e, x, y, node, view }) => {
        console.log('node:click', e, x, y, node, view)
        if (this.isEditMode) {
          console.log(``)
        } else {
          console.log(``)
          this.currentTitle = node.attrs.label.text
          this.handleOpenDialog(e)
        }
      })
      //  attrs 
      graph.on('node:dblclick', ({ e, x, y, node, view }) => {
        console.log('node:dblclick', e, x, y, node, view)
        this.visible = true
        this.currentNode = node
        this.form.label = node.label
        this.form.color = node.attrs.body.fill
      })
    },
    // 
    initNode() {
      const ports = {
        groups: {
          top: {
            position: 'top',
            attrs: {
              circle: {
                r: 4,
                magnet: true,
                stroke: '#5F95FF',
                strokeWidth: 1,
                fill: '#fff',
                style: {
                  visibility: 'hidden',
                },
              },
            },
          },
          right: {
            position: 'right',
            attrs: {
              circle: {
                r: 4,
                magnet: true,
                stroke: '#5F95FF',
                strokeWidth: 1,
                fill: '#fff',
                style: {
                  visibility: 'hidden',
                },
              },
            },
          },
          bottom: {
            position: 'bottom',
            attrs: {
              circle: {
                r: 4,
                magnet: true,
                stroke: '#5F95FF',
                strokeWidth: 1,
                fill: '#fff',
                style: {
                  visibility: 'hidden',
                },
              },
            },
          },
          left: {
            position: 'left',
            attrs: {
              circle: {
                r: 4,
                magnet: true,
                stroke: '#5F95FF',
                strokeWidth: 1,
                fill: '#fff',
                style: {
                  visibility: 'hidden',
                },
              },
            },
          },
        },
        items: [
          {
            group: 'top',
          },
          {
            group: 'right',
          },
          {
            group: 'bottom',
          },
          {
            group: 'left',
          },
        ],
      }

      // NOTE: 
      Graph.registerNode(
        'custom-rect',
        {
          inherit: 'rect',
          width: 120,
          height: 40,
          attrs: {
            label: {
              text: '',
              fill: '#FFF',
            },
            body: {
              rx: 4,
              ry: 4,
              strokeWidth: 1,
              stroke: '#2590FF', // 
              fill: '#2590FF', // 
            },
            text: {
              fontSize: 16,
              fill: '#FFF',
            },
          },
          ports: { ...ports },
        },
        true,
      )
      // NOTE: 
      Graph.registerNode(
        'custom-circle',
        {
          inherit: 'circle',
          width: 120,
          height: 120,
          attrs: {
            label: {
              text: '',
              fill: '#FFF',
            },
            body: {
              strokeWidth: 1,
              stroke: '#2590FF',
              fill: '#1681F1',
            },
            text: {
              fontSize: 16,
              fill: '#FFF',
            },
          },
          ports: { ...ports },
        },
        true,
      )
      // NOTE: 
      Graph.registerNode(
        'custom-rect-small',
        {
          inherit: 'rect',
          width: 128,
          height: 20,
          attrs: {
            label: {
              text: '',
              fill: '#FFF',
            },
            body: {
              rx: 4,
              ry: 4,
              strokeWidth: 1,
              stroke: '#2590FF',
              fill: '#2590FF',
            },
            text: {
              fontSize: 12,
              fill: '#262626',
            },
          },
          ports: { ...ports },
        },
        true,
      )
    },
    // 
    initStencil(graph) {
      const stencil = new Stencil({
        title: '',
        target: graph,
        stencilGraphWidth: 200,
        stencilGraphHeight: 800,
        // stencilGraphOptions: { panning: true },
        collapsable: true,
        groups: [
          {
            title: '',
            name: 'group1',
          },
          // {
          //   title: '',
          //   name: 'group2',
          //   graphHeight: 250,
          //   layoutOptions: {
          //     rowHeight: 70,
          //   },
          // },
        ],
        layoutOptions: {
          columns: 1,
          columnWidth: 200,
          rowHeight: 100,
        },
      })

      document.getElementById('stencil')?.appendChild(stencil.container)

      const square = graph.createNode({
        shape: 'custom-rect',
        label: '',
      })

      const round = graph.createNode({
        shape: 'custom-circle',
        label: '',
      })

      const squareBySmall = graph.createNode({
        shape: 'custom-rect-small',
        label: '',
      })

      stencil.load([square, round, squareBySmall], 'group1')
    },
    onSave() {
      let data = this.graphObj.toJSON()
      let data2 = this.graphObj.parseJSON(data)
      localStorage.setItem(`x6JSON`, JSON.stringify(data2))
      this.$message.success(``)
    },
    onLoadBylocalStorage() {
      let cache = localStorage.getItem(`x6JSON`)
      let jsonData = JSON.parse(cache)
      let currentData = {
        nodes: [],
        edges: [],
      }
      jsonData.forEach(x => {
        if (x.shape !== 'edge') {
          currentData.nodes.push(x)
        } else {
          currentData.edges.push(x)
        }
      })
      this.graphObj.fromJSON(currentData) // 
      this.graphObj.zoomTo(0.5)
      this.graphObj.centerContent() // 
    },
    onLoadByDefaultData() {
      let currentData = {
        nodes: [],
        edges: [],
      }
      nodeData.forEach(x => {
        if (x.shape !== 'edge') {
          currentData.nodes.push(x)
        } else {
          currentData.edges.push(x)
        }
      })
      this.graphObj.fromJSON(currentData) // 
      this.graphObj.zoomTo(0.5)
      this.graphObj.centerContent() // 
    },
    onConfirm() {
      this.currentNode.setAttrs({
        label: {
          text: this.form.label,
        },
        body: {
          fill: this.form.color,
          stroke: this.form.color,
        },
      })
      this.visible = false
    },
    onCancel() {
      this.visible = false
    },
    onTriggerIsEdgeWithArrow() {
      this.isEdgeWithArrow = !this.isEdgeWithArrow
      this.$message.success(this.isEdgeWithArrow ? '' : '')
    },
    handleOpenDialog(event) {
      const { clientX, clientY } = event
      //  dialog  10px 
      const left = clientX + 10
      const top = clientY - 250

      // 
      const maxWidth = window.innerWidth - 300 // 300  dialog width
      const maxHeight = window.innerHeight - 200 // 

      this.dialogStyle.left = Math.min(left, maxWidth) + 'px'
      this.dialogStyle.top = Math.min(top, maxHeight) + 'px'
      this.dialogVisible = true
    },
    onOpened() {
      //  dialog  fixed  Element 
      const dialog = this.$el.querySelector('.mouse-position-dialog')
      if (dialog) {
        dialog.style.position = 'fixed'
        dialog.style.margin = '0'
      }
    },
    onTriggerByLine(lineType) {
      this.lineType = lineType
    },
  },
}
</script>

<style lang="less" scoped>
